
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  height: 100%;
}
html, body{
  height: 100%;
}

#parent{
  height: 50px;
  position: relative;
}
#logo {
  font-weight: bold;
  position : absolute;
  left: 10px;
}
#date {
  position : absolute;
  right: 190px;
}
#user {
  font-weight: bold;
  position : absolute;
  right: 80px;
}
#logout {
  position : absolute;
  right: 10px;
  top: 10px;
}

#login{
  background-image: url(/static/img/food2.9ef4bd6.png);
  background-position: center;
  height: 100%;
  font-size: 20px;
  font-weight: bold;
  color: #FFFFFF;
}

#main{
  height: 100%;
  position: relative;
}
#goodsInput{
  background-color: #7FFFD4;
  width: 60%;
  height: 100%;
  position : absolute;
  right: 0px;
}
#goodsList{
  background-color: #00FFFF;
  width: 40%;
  height: 80%;
  position : absolute;
  left: 0px;
  /* margin-left: 0px; */
}
#goodsFooter{
  background-color: #5F9EA0;
  width: 40%;
  height: 20%;
  position : absolute;
  left: 0px;
  bottom: 0px;
  /* margin-left: 0px; */
}






















































































